"use client";
import { useState, useEffect } from "react";
import { Navbar } from "../../components/Navbar";
import { Calendar, Search, Filter, Play, RefreshCw, AlertCircle } from "lucide-react";

interface ScanResult {
    symbol: string;
    delivery_percent: number;
    volume_multiplier: number;
    price_change_pct: number;
    score: number;
}

export default function ScannerPage() {
    // State
    const [date, setDate] = useState<string>(new Date().toISOString().split('T')[0]);
    const [status, setStatus] = useState<'idle' | 'running' | 'completed' | 'failed'>('idle');
    const [results, setResults] = useState<ScanResult[]>([]);
    const [loading, setLoading] = useState(false);

    // Filters
    const [minDelivery, setMinDelivery] = useState(40);
    const [minVol, setMinVol] = useState(1.5);
    const [maxPrice, setMaxPrice] = useState(5.0);

    // API Base URL (Proxy should be set up or CORS enabled)
    const API_URL = "http://localhost:3001/scanner";

    const runScan = async () => {
        try {
            setStatus('running');
            setLoading(true);

            // Trigger Run
            const res = await fetch(`${API_URL}/run`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ date })
            });
            const data = await res.json();

            if (data.status === 'running' || data.status === 'completed') {
                pollStatus();
            } else {
                setStatus('failed');
                setLoading(false);
            }
        } catch (e) {
            console.error(e);
            setStatus('failed');
            setLoading(false);
        }
    };

    const pollStatus = async () => {
        const interval = setInterval(async () => {
            try {
                const res = await fetch(`${API_URL}/status?date=${date}`);
                const data = await res.json();

                if (data.status === 'completed') {
                    clearInterval(interval);
                    setStatus('completed');
                    fetchResults();
                } else if (data.status === 'failed') {
                    clearInterval(interval);
                    setStatus('failed');
                    setLoading(false);
                }
            } catch (e) {
                clearInterval(interval);
            }
        }, 2000);
    };

    const fetchResults = async () => {
        try {
            const query = new URLSearchParams({
                date,
                min_delivery_pct: minDelivery.toString(),
                min_vol_multiplier: minVol.toString(),
                max_price_pct: maxPrice.toString()
            });

            const res = await fetch(`${API_URL}/results?${query}`);
            const data = await res.json();
            setResults(data);
        } catch (e) {
            console.error(e);
        } finally {
            setLoading(false);
        }
    };

    // Re-fetch results when filters change if status is completed
    useEffect(() => {
        if (status === 'completed') {
            fetchResults();
        }
    }, [minDelivery, minVol, maxPrice]);

    return (
        <div className="min-h-screen bg-black text-white font-sans">
            <div className="fixed inset-0 bg-[url('/noise.svg')] opacity-[0.03] pointer-events-none z-0"></div>
            <Navbar />

            <main className="pt-24 px-6 max-w-7xl mx-auto flex flex-col md:flex-row gap-8 relative z-10 h-[calc(100vh-6rem)]">

                {/* Sidebar Filters */}
                <aside className="w-full md:w-80 flex-shrink-0 space-y-8 p-6 rounded-3xl bg-zinc-900/50 border border-white/5 backdrop-blur-xl h-fit">
                    <div>
                        <h2 className="text-xl font-bold flex items-center gap-2 mb-6 text-emerald-400">
                            <Filter className="w-5 h-5" /> Config
                        </h2>

                        {/* Date Picker */}
                        <div className="space-y-3 mb-6">
                            <label className="text-sm text-zinc-400 font-medium">Trading Date</label>
                            <div className="relative">
                                <Calendar className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-zinc-500" />
                                <input
                                    type="date"
                                    value={date}
                                    onChange={(e) => setDate(e.target.value)}
                                    className="w-full bg-black/50 border border-white/10 rounded-xl py-2.5 pl-10 pr-4 text-sm focus:outline-none focus:border-emerald-500/50 transition-colors"
                                />
                            </div>
                        </div>

                        <button
                            onClick={runScan}
                            disabled={loading || status === 'running'}
                            className={`w-full py-4 rounded-xl font-bold text-lg shadow-lg transition-all ${loading
                                    ? 'bg-zinc-800 text-zinc-500 cursor-not-allowed'
                                    : 'bg-gradient-to-r from-emerald-500 to-teal-400 hover:scale-[1.02] active:scale-[0.98] text-white shadow-emerald-500/20'
                                }`}
                        >
                            {loading ? (
                                <span className="flex items-center justify-center gap-2">
                                    <RefreshCw className="w-5 h-5 animate-spin" /> Running...
                                </span>
                            ) : (
                                <span className="flex items-center justify-center gap-2">
                                    <Play className="w-5 h-5 fill-current" /> Run Scanner
                                </span>
                            )}
                        </button>
                    </div>

                    <div className="space-y-6 pt-6 border-t border-white/5">
                        <div className="space-y-3">
                            <div className="flex justify-between text-sm">
                                <span className="text-zinc-400">Min Delivery %</span>
                                <span className="text-emerald-400 font-mono">{minDelivery}%</span>
                            </div>
                            <input
                                type="range"
                                min="0" max="100"
                                value={minDelivery}
                                onChange={(e) => setMinDelivery(Number(e.target.value))}
                                className="w-full accent-emerald-500 h-2 bg-zinc-800 rounded-lg appearance-none cursor-pointer"
                            />
                        </div>

                        <div className="space-y-3">
                            <div className="flex justify-between text-sm">
                                <span className="text-zinc-400">Min Volume Spike</span>
                                <span className="text-purple-400 font-mono">{minVol}x</span>
                            </div>
                            <input
                                type="range"
                                min="1" max="10" step="0.1"
                                value={minVol}
                                onChange={(e) => setMinVol(Number(e.target.value))}
                                className="w-full accent-purple-500 h-2 bg-zinc-800 rounded-lg appearance-none cursor-pointer"
                            />
                        </div>

                        <div className="space-y-3">
                            <div className="flex justify-between text-sm">
                                <span className="text-zinc-400">Max Price Change</span>
                                <span className="text-blue-400 font-mono">+/- {maxPrice}%</span>
                            </div>
                            <input
                                type="range"
                                min="0" max="20" step="0.5"
                                value={maxPrice}
                                onChange={(e) => setMaxPrice(Number(e.target.value))}
                                className="w-full accent-blue-500 h-2 bg-zinc-800 rounded-lg appearance-none cursor-pointer"
                            />
                        </div>
                    </div>
                </aside>

                {/* Main Results Area */}
                <section className="flex-1 bg-zinc-900/30 border border-white/5 rounded-3xl backdrop-blur-md flex flex-col overflow-hidden">
                    <div className="p-6 border-b border-white/5 flex justify-between items-center">
                        <h1 className="text-2xl font-bold text-white">Scanner Results</h1>
                        <div className="text-sm text-zinc-500">
                            {results.length} stocks found
                        </div>
                    </div>

                    <div className="flex-1 overflow-auto p-0">
                        {loading && results.length === 0 ? (
                            <div className="h-full flex flex-col items-center justify-center text-zinc-500 space-y-4">
                                <RefreshCw className="w-10 h-10 animate-spin opacity-50" />
                                <p>Analyzing market data...</p>
                            </div>
                        ) : results.length === 0 ? (
                            <div className="h-full flex flex-col items-center justify-center text-zinc-600 space-y-4">
                                <Search className="w-12 h-12 opacity-20" />
                                <p>No results found for these criteria.</p>
                            </div>
                        ) : (
                            <table className="w-full text-left border-collapse">
                                <thead className="bg-white/5 sticky top-0 backdrop-blur-md z-10 text-xs uppercase tracking-wider text-zinc-400 font-medium">
                                    <tr>
                                        <th className="p-4 pl-6">Symbol</th>
                                        <th className="p-4 text-right">Delivery %</th>
                                        <th className="p-4 text-right">Vol Spike</th>
                                        <th className="p-4 text-right">Price %</th>
                                        <th className="p-4 text-right pr-6">Score</th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-white/5 text-sm">
                                    {results.map((row) => (
                                        <tr key={row.symbol} className="hover:bg-white/5 transition-colors group">
                                            <td className="p-4 pl-6 font-bold text-white group-hover:text-emerald-300 transition-colors">{row.symbol}</td>
                                            <td className={`p-4 text-right font-mono ${row.delivery_percent > 60 ? 'text-emerald-400' : 'text-zinc-300'}`}>
                                                {Number(row.delivery_percent).toFixed(1)}%
                                            </td>
                                            <td className={`p-4 text-right font-mono ${row.volume_multiplier > 3 ? 'text-purple-400' : 'text-zinc-300'}`}>
                                                {Number(row.volume_multiplier).toFixed(2)}x
                                            </td>
                                            <td className={`p-4 text-right font-mono ${Math.abs(row.price_change_pct) < 1 ? 'text-blue-400' : 'text-zinc-300'}`}>
                                                {row.price_change_pct > 0 ? '+' : ''}{Number(row.price_change_pct).toFixed(2)}%
                                            </td>
                                            <td className="p-4 text-right pr-6 text-zinc-500 font-mono">
                                                {Number(row.score).toFixed(0)}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        )}
                    </div>
                </section>
            </main>
        </div>
    );
}
